.page {
  height: 100%;
  font-family: var(--font-family);
  font-weight: 400;
  font-style: normal;
  font-feature-settings: "pnum" on, "lnum" on;
  font-size: 1.3vw;
  line-height: 1.6;
  letter-spacing: 0.04em;
  -webkit-text-size-adjust: 100%;
  color: var(--color-white);
  background-color: var(--color-bg);
  scroll-behavior: smooth;
  overscroll-behavior-y: none;
}
.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
}
@media (min-width: 120em) {
  .page {
    font-size: 22px;
  }
}
@media (max-width: calc(80.0625em - 0.0625em)) {
  .page {
    font-size: 16px;
  }
}

.site-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.sr-only {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.section {
  margin-bottom: clamp(100px, 11.875rem, 190px);
}
@media (max-width: calc(90.0625em - 0.0625em)) {
