}
.main--clean {
  padding-top: 0;
}

.map {
  position: relative;
  margin: 0 auto;
}
.map::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-black);
  opacity: 0.4;
}
.map__image {
  width: 100%;
}

.scroll {
  overflow: auto;
  padding-bottom: 10px;
}
.scroll::-webkit-scrollbar {
