}
.scroll::-webkit-scrollbar-track {
  -webkit-border-radius: 10px;
  border-radius: 10px;
  background-color: var(--color-white);
}
.scroll::-webkit-scrollbar-thumb {
  -webkit-border-radius: 10px;
  border-radius: 10px;
  background: var(--color-active);
}

.header {
  position: fixed;
  top: 0;
  z-index: 10;
  border-bottom: 0.5px solid var(--color-black);
  padding: 35px 0 30px;
