.hero {
  background-image: url("../img/hero.jpg.jpg");
  /* stylelint-disable-next-line declaration-block-no-duplicate-properties */
  background-image: image-set("../img/hero.jpg.avif" type("image/avif"), "../img/hero.jpg.webp" type("image/webp"), "../img/hero.jpg.jpg" type("image/jpg"));
  display: flex;
  align-items: center;
  position: relative;
  min-height: 100vh;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.hero::before {
  content: "";
  position: absolute;
  z-index: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-black);
  opacity: 0.36;
  filter: blur(5px);
}
.hero__content {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-top: 4.0625rem;
  height: 100vh;
}
.hero__title {
  margin-top: auto;
  margin-bottom: 10px;
}
.hero__text {
  margin-bottom: 2.625rem;
  letter-spacing: 0.04em;
}
.hero__mouse {
  position: relative;
  display: block;
  margin: auto auto 2.1875rem;
  border: 2px solid var(--color-white);
  border-radius: 100px;
  width: 40px;
  height: 60px;
}
.hero__mouse::before {
  content: "";
  position: absolute;
  left: 1px;
  right: 0;
  margin: 0 auto;
  width: 2px;
  height: 19px;
  background-color: var(--color-white);
  animation: mouse 2s linear 0.1s infinite;
}

@keyframes mouse {
  0% {
    top: 5px;
    opacity: 0;
  }
  100% {
    top: 30px;
    opacity: 1;
  }
}
